html {
  box-sizing: border-box;
  font-size: 100%;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  background-color: #d0d3d6;
  position: relative;
  font-family: Work Sans, Helvetica, sans-serif;
  font-weight: 400;
  font-size: 1em;
  color: #121212;
  line-height: 1.5;
  margin: 0;
}

article,
aside,
canvas,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
  width: 100%;
}

ul,
li {
  margin: 0;
  padding: 0;
  list-style: outside none none;
}

a {
  color: #906932;
  text-decoration: none;
}

a:visited {
  color: #906932;
  text-decoration: none;
}

a:hover, a:active {
  outline: none;
  color: #056ac1;
  text-decoration: underline;
}

select:hover,
select:focus {
  outline: none;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}

h1 {
  font-size: 1.44em;
}

h2 {
  font-size: 1.4em;
}

h3 {
  font-size: 1.2em;
}

h4 {
  font-size: 1.167em;
}

h5 {
  font-size: 1em;
}

h6 {
  font-size: 0.972em;
}

/* Centered flex-grid*/
.primary-nav,
.footer-grid {
  display: flex;
  flex-flow: row wrap;
  align-items: center;
  justify-content: center;
  padding: 0.75rem;
  width: 100%;
}

/* Common to both pages*/
.main-content {
  min-height: 100%;
}

/*Common styles*/
.primary-nav {
  background-color: #ffffff;
  border-bottom: 1px solid #e0e0e0;
  min-height: 4.5rem;
}

.header-title {
  font-size: 1.313em;
  text-transform: uppercase;
  font-weight: 500;
  text-align: left;
}

.header-link {
  color: #121212;
}

.header-link:hover,
.header-link:focus {
  text-decoration: none;
}

.footer-grid {
  font-size: 0.694em;
  letter-spacing: 1px;
  text-transform: uppercase;
  border-top: 1px solid rgba(0, 0, 0, 0.1);
  min-height: 3.125rem;
  padding: 0.75rem;
  text-align: center;
}

.footer-link {
  padding: 0 8px;
  font-weight: 500;
  color: #424242;
}

/* Common styles*/
/* Accessibility link to skip map*/
.skip-main {
  left: -999px;
  position: absolute;
  top: auto;
  width: 1px;
  height: 1px;
  overflow: hidden;
  z-index: -999;
}

.skip-main:focus {
  background: #fbfbfb;
  border: 3px solid;
  left: auto;
  top: auto;
  width: auto;
  height: auto;
  overflow: auto;
  margin: 1.5rem 1.5rem 1.5rem 2.25rem;
  padding: 0.75rem;
  text-align: center;
  font-size: 1em;
  text-decoration: none;
  z-index: 999;
}

.toggle {
  background-color: transparent;
  padding: 0.5em 1em;
  text-decoration: none;
  color: #212121;
  border: 2px solid #212121;
  display: inline-block;
}

.toggle:hover {
  text-decoration: none;
  background-color: white;
  border: 2px solid white;
  color: #96999c;
}

#map {
  height: 22.5rem;
  width: 100%;
  border: 12px solid #fbfbfb;
}

.restaurant-img {
  margin: 0;
  width: 100%;
  display: block;
  max-width: 100%;
  min-width: 100%;
}

/* ====================== Grid ====================== */
#filter-section {
  background: #ffffff;
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
}

#filter-section:focus {
  outline: none;
}

#filter-section:active {
  outline: none;
}

.filter-grid,
.restaurants-grid {
  display: flex;
  flex-flow: row wrap;
  width: 100%;
  margin: 0 auto;
  max-width: 1280px;
  padding: 1.5rem;
}

@media (min-width: 600px) {
  .filter-grid {
    padding: 3rem 1.5rem;
    max-width: 800px;
    margin: 0 auto;
  }
}

.restaurants-grid {
  padding: 0.75rem;
}

/* No flex*/
.map-grid {
  max-width: 1280px;
  margin: 0 auto;
  padding: 1.5rem 1.5rem 0;
  width: 100%;
}

.map-box {
  width: 100%;
  display: none;
}

.map-box.is-visible {
  display: block;
  margin: 1.5rem 0 0;
}

.btn-cell {
  display: block;
}

/* ====================== Restaurant Filtering ====================== */
.title-sm {
  margin: 0 0 0.75rem;
  width: 100%;
  font-size: 1em;
  font-weight: 500;
  text-transform: uppercase;
}

.label {
  font-size: 0.81em;
  color: #4e4947;
  display: block;
}

@media (min-width: 600px) {
  .label {
    margin-top: 0.75rem;
  }
}

@media (min-width: 840px) {
  .label {
    font-size: 0.833em;
  }
}

.select-bar {
  background-color: #ffffff;
  font-family: Work Sans, sans-serif;
  font-size: 0.833em;
  height: 2.625rem;
  letter-spacing: 0;
  margin: 0.125rem 0 0.75rem 0;
  padding: 0 0.75rem;
  width: 100%;
  border: 2px solid #e0e0e0;
  display: block;
  border-radius: 5px;
}

@media (min-width: 840px) {
  .select-bar {
    font-size: 0.972em;
  }
}

.select-bar:focus, .select-bar:hover {
  background-color: #f4f5f6;
}

/* Display filter side by side */
.results-filter {
  width: 100%;
  margin: 0.75rem 0 0;
}

@media (min-width: 600px) {
  .results-filter {
    width: calc(50% - 0.75rem);
    margin: 0 0.75rem 0 0;
  }
}

@media (min-width: 600px) {
  .results-filter:last-child {
    margin: 0 0 0 0.75rem;
  }
}

/* ====================== Restaurant Listing ====================== */
.restaurant-list-item {
  margin: 0.75rem;
  width: calc(100% - 1.5rem);
  background: #fbfbfb;
  /* Display two restaurant cards per row*/
}

@media (min-width: 600px) {
  .restaurant-list-item {
    margin: 0.75rem;
    width: calc(50% - 1.5rem);
  }
}

@media (min-width: 600px) {
  .restaurant-list-item:last-child {
    margin: 0.75rem;
  }
}

@media (min-width: 960px) {
  .restaurant-list-item {
    width: calc(33.33% - 1.5rem);
  }
}

.flex-card-content {
  padding: 1.5em;
  background: #fbfbfb;
}

.restaurant-list-heading {
  margin-bottom: 0.75rem;
  font-weight: 500;
}

@media (min-width: 720px) {
  .restaurant-list-heading {
    font-size: 1.4em;
  }
}

.neighborhood {
  margin: 0 0 0.375rem;
  font-size: 0.833rem;
  text-transform: uppercase;
  /*@media (min-width: 720px){
        font-size: 1.4em;
    }*/
}

.address {
  margin: 0 0 0.75rem;
  font-size: 0.833rem;
}

@media (min-width: 720px) {
  .address {
    font-size: 1em;
  }
}

.restaurant-link {
  font-size: 0.833em;
  text-decoration: none;
}

@media (min-width: 720px) {
  .restaurant-link {
    font-size: 1em;
  }
}
