/* Common styles*/
/* Accessibility link to skip map*/
.skip-main {
        left: -999px;
        position: absolute;
        top: auto;
        width: 1px;
        height: 1px;
        overflow: hidden;
        z-index: -999;
}

.skip-main:focus {
    background: #fbfbfb;
    border: 3px solid;
    left: auto;
    top: auto;
    width: auto;
    height: auto;
    overflow: auto;
    margin: 1.5rem 1.5rem 1.5rem 2.25rem;
    padding: 0.75rem;
    text-align: center;
    font-size: 1em;
    text-decoration: none;
    z-index: 999;
}

.toggle {
    background-color: transparent;
    padding: 0.5em 1em;
    text-decoration: none;
    color: #212121;
    border: 2px solid #212121;
    display: inline-block;
    &:hover {
        text-decoration:none;
        background-color: white;
        border: 2px solid white;
        color: #96999c;
    }
}